.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

/* ======= CUSTOM STYLES ======== */

input:invalid {  background-color: #ffdddd; }

.navbar{
	width:100%;
	font-family:"Helvetica Neue",Helvetica,Arial,sans-serif;
	height:35px;
	/*background-color:#080808;*/
	/*color:#428bca;*/
	background-color: #858383;
	color:#9dfcdb;
	font-size:24px;
	line-height:35px;
	cursor:default;
	-webkit-box-shadow:0 0 5px rgba(0,0,0,.8);
	        box-shadow:0 0 5px rgba(0,0,0,.8);
	position:relative;
	z-index:1;
	margin-top:-2px;
}

#canvasWrapper {
	  overflow:hidden;
	  height:70vh;
	  position:relative;
}

.bottom{
	font-size:14px;
	width:100%;
}

/*#buttons { background-color: brown; } */

.button {
	padding:10px;
	position:relative;
	border-radius:10px;
	z-index: 2;
	cursor:pointer;
	/*font-weight:bold;*/
	font-size:1.1em;
	display:inline-block;
	-webkit-box-shadow:0 0 10px rgba(0,0,0,.8);
	        box-shadow:0 0 10px rgba(0,0,0,.8);	
}

.centered { margin: 0 auto; display:inline-block; }

#FreezeButton {
	background: blue;
	color:white;
	margin-right:10px;
}

#FreezeButton:active, #FreezeButton:hover {
	background:#081a57; 
}

#referenceLink {
	background-color:#e02e0b;
	color:white;
}

#referenceLink:active, #referenceLink:hover {
	background-color:#610707;
}

#rightMenuButton { float:right; margin-right:30px; background:white;}
#leftMenuButton { float:left; margin-left:30px; background:white;}

#rightMenuButton:active, #rightMenuButton:hover, #leftMenuButton:active, #leftMenuButton:hover {
	background:#a9b2d4;	
}

.smallBreak { line-height:8px; }

#scaleunits { /*vertical-align:top;*/
	position:fixed;
	top:40px;
	left:15px;
}

#playback {
	z-index:2;
	font-size:0.9em;
	position:fixed;
	top:40px;
	right:15px;
	width:153px;
}

.bubble {     /*lies within the fixed ".bottom" */
    margin:0 auto;
	font-size:13px;
	background-color:white;
	border-radius:10px;
	padding:4px;	
	width:290px;
	z-index:1;
	position:relative;
}

.InputStyle {
	font-size:17px;
	padding:1px 3px;
	width:80px;
}

#angle { width:49px; }

/*130 works in Chrome, but looks terrible in FireFox.*/
.slider{ width:124px; }

.UnitsStyle {
	display:inline-block;
	padding:2px 5px 4px 5px;
	background-color:#ffefdb;
	width:25px;
	font-size:17px;
	color:#000000;
	border-radius:4px;
}

.InputBox {
	margin-top:17px;
	padding-top:17px;
	text-align:left;
	width:281px;
	border-top:1px dotted blue;
	/*margin-right:-10px;*/
}

.range {
	font-size:11px;
	color:white;
}

.randomFact{
	font-size:14px;
	width:125px;
	color:#adccff;
	padding: 0px 2px;
	display:inline-block;
}

.RadioBox {
	font-size:14px;
	text-align:center;
}

.ResetButton {
	background-color: #4CAF50; /* Green */
	border: none;
	color: white;
	padding: 10px 32px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 16px;
	border-radius: 5px;
	margin-top:20px;
}

/*Remove the useless {0} value bubble in Edge browser.*/
::-ms-tooltip { display: none; }

.hide { display:none; }

#outer-container {
	height:100vh;
	overflow:hidden;
}

.distortionwarning { margin: 10px;
					font-size:0.8em; 
					color:#f49842; }

@keyframes move-stars-back {
    from {background-position:0 0;}
    to {background-position:10000px 0;}
}
@-webkit-keyframes move-stars-back {
    from {background-position:0 0;}
    to {background-position:10000px 0;}
}					
					
					
.stars {
	background:url(img/stars.jpg) repeat top center;
	position:absolute;
	top:0;
	left:0;
	right:0;
	bottom:0;
	width:100%;
	height:100%;
	display:block;
	z-index:0;
  -webkit-animation:move-stars-back 200s linear infinite;
  animation:move-stars-back 200s linear infinite;
}					
					
